<template>
  <div>
    <Loader v-if="loading"/>
    <p class="center" v-else-if="!record">Запись не найдена</p>
    <div v-else>
      <div class="breadcrumb-wrap">
        <router-link class="breadcrumb" to="/history">История</router-link>
        <a @click.prevent class="breadcrumb">
          {{record.typeTitle}}
        </a>
      </div>
      <div class="row" >
        <div class="col s12 m6">
          <div class="card" :class="record.class">
            <div class="card-content white-text">
              <p>Описание: {{ record.description }}</p>
              <p>Сумма: {{ record.amount | currencyFilter}}</p>
              <p>Категория: {{ record.title }}</p>

              <small>{{ record.date.slice(0,10) }}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detail',
  data:()=>({
    loading: true,
    record: null,
  }),
  async mounted(){
    const id = this.$route.params.recordID
    const record = await this.$store.dispatch('fetchRecordByID', id)
    const category = await this.$store.dispatch('fetchCategoryByID', record.categoryID)
    
    this.record = {
      ...record,
      title: category.title,
      class: record.type === 'outcome'? 'red': 'green',
      typeTitle: record.type === 'outcome'? 'Расход': 'Доход'
    }
    this.loading = false 
  },

}
</script>